.asidebar-container {
  width: 300px;
  height: 100vh;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  position: relative;
  /* border: 1px solid green; */
}

.aside-header {
  font-size: 2rem;
  margin: 2.5rem 0;
}

.shortcut-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 2rem;
}

.shortcut-cards {
  display: flex;
  align-items: center;
  font-size: 1rem;
  gap: 0.2rem;
  padding: 1rem 1.3rem;
  border-radius: 1rem;
  background-color: var(--white-color);
  cursor: pointer;
}

.favorite-artist-section {
  overflow-y: auto;
  height: calc(100% - (5rem) + 95px);
}

.fav-artist-container {
  display: flex;
  align-items: center;
  gap: 0.2rem;
  margin-bottom: 5px;
}

.fav-artist-container .image {
  width: auto;
  height: 40px;
  border-radius: 50%;
  margin-right: 1rem;
}

.fav-artist-container .image img {
  display: block;
  height: 100%;
  width: fit-content;
  border-radius: 50%;
}

.fav-artist-container .fav-artist-text .lead-text {
  font-size: 1.5rem;
  margin-bottom: 5px;
}

.fav-artist-container .fav-artist-text .sub-text {
  font-size: 1rem;
  font-weight: 300;
}

.fav-artist-container .fav-artist-icon {
  align-items: flex-end;
  position: absolute;
  right: 20px;
  /* margin-left: 7rem; */
}

.bottom-container {
  background-color: var(--white-color);
  border-radius: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 20px;
  left: 0.5rem;
  align-self: flex-end;
  /* height: 120px; */
}

.image-container {
  height: 110px;
  width: 100%;
  border-radius: 1rem;
}

.image-container img {
  height: 100%;
  width: 100%;
  border-radius: 1rem;
}
.bottom-text-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.aside-lead-text {
  margin-bottom: 4px;
}

.aside-sub-text {
  font-weight: 300;
}

.aside-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

@media (max-width: 800px) {
  .asidebar-container {
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
  }
}
